<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Center',query:{typeName:'Center',index:0}}">学生中心</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:typeName,query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <ul class="itemUl">
        <div v-if="List[index] && List[index].length > 0">
          <li v-for="(list,index1) in List[index]" :key="index1">
            <router-link :to="{name:'CenterDetail',query:{typeName,index,id:list.id}}" class="overPointr1">
              {{list.courseName}}
            </router-link>
            <span class="time">{{list.courseTime}}</span>
          </li>
        </div>
        <div class="emptyVideos" v-else>
          此栏目下没有数据
        </div>
      </ul>
      <div class="page">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="pageNum"
          :page-size="pageSize"
          :hide-on-single-page="page.pages < 2"
          layout="total, prev, pager, next"
          :total="page.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        pageNum:1,               //当前页数
        pageSize:10,             //当前页行数
        showFlag:1,              //显示标志，1显示，0废弃
        page:{                   //页数等信息
          total:0,               //总条数
          size:0,                //当页的行数
          pages:0,               //总页数
        },

        List:[],                 //资源列表
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Center',       //当前菜单栏选项
        index:0,                 //第几个小菜单
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //分页
      handleCurrentChange(num){
        console.log(num)
      },

      //更新数据显示
      handleIndex(){
        var {menus,typeName,index} = this
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleIndex()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Center',
          menuName:'16级学生课程报告',
          name:'CenterDetail'
        },
        {
          typeName:'Center',
          menuName:'17级学生课程报告',
          name:'CenterDetail'
        },
        {
          typeName:'Center',
          menuName:'18级学生课程报告',
          name:'CenterDetail'
        },
        {
          typeName:'Center',
          menuName:'19级学生课程报告',
          name:'CenterDetail'
        },
      ]
      //当前栏目下的各个菜单包含的数据
      this.List = [
        [
          {
            "id": "0",
            "courseName": "第一组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "1",
            "courseName": "第二组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "2",
            "courseName": "第三组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "3",
            "courseName": "第四组实验报告",
            "courseTime": "2020-09-28"
          },
        ],
        [
          {
            "id": "4",
            "courseName": "第五组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "5",
            "courseName": "第六组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "6",
            "courseName": "第七组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "7",
            "courseName": "第八组实验报告",
            "courseTime": "2020-09-29"
          },
        ],
        [
          {
            "id": "0",
            "courseName": "第一组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "1",
            "courseName": "第二组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "2",
            "courseName": "第三组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "3",
            "courseName": "第四组实验报告",
            "courseTime": "2020-09-28"
          },
        ],
        [
          {
            "id": "4",
            "courseName": "第五组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "5",
            "courseName": "第六组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "6",
            "courseName": "第七组实验报告",
            "courseTime": "2020-09-28"
          },
          {
            "id": "7",
            "courseName": "第八组实验报告",
            "courseTime": "2020-09-29"
          },
        ]
      ]
      //初始化数据
      this.handleIndex()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        ul.itemUl{
          padding: 0;
          min-height: 470px;
          margin: 0;
          li{
            list-style: none;
            height: 45px;
            line-height: 45px;
            border-bottom: 1px dashed #D7D7D7;
            padding-left: 15px;
            &:before,&:after{
              content: '';
              display: table;
              clear: both;
            }
            a{
              float: left;
              display: inline-block;
              color: #333;
              max-width: calc(100% - 100px);
              &:hover{
                text-decoration: none;
                color: red;
              }
            }
            .time{
              float: right;
              color: #999;
            }
          }
          .emptyVideos{
            color: #333;
            text-indent: 2em;
            font-size: 14px;
            line-height: 23px;
            min-height: 100px;
          }
        }
        .page{
          text-align: center;
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          ul{
            li{
              font-size: 14px;
              padding-left: 0px;
              &:before,&:after{
                content: '';
                display: table;
                clear: both;
              }
              a{
                float: left;
                display: inline-block;
                color: #333;
                max-width: calc(100% - 100px);
                &:hover{
                  text-decoration: none;
                  color: red;
                }
              }
              .time{
                float: right;
                color: #999;
              }
            }
          }
        }
      }
    }
  }
</style>



